.header {
  padding: 10px 0;
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 30px 0;
  transition: all .5s linear 0s;
  &.active {
    top: -140px;
    padding: 10px 0;
    background: $white;
    box-shadow: 5px 5px 15px 0 rgba(31, 31, 31, 0.07); }
  &.scroll {
    top: 0; }
  @include r(767) {
    top: 0;
    padding: 15px 0; }
  &__logo {
    @include r(767) {
      position: relative;
      top: 4px; }
    img {
      height: 40px; } } }
